@import url(https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap);
.app {
  height: 100vh;
  background-image: url(/static/media/balls.6d7663d8.jpg);
  background-size: cover;
  display: flex;
}

.input{
  display: inline-block;
  width: 85%;
  font-size:25px;
  padding: 10px;
  margin-left: 15px;
}
.btn{
  position: relative;
  left: -50px;
  top: 10px;
  display: inline-block;
  font-size:40px;
  background: none;
  padding: 5px;
  outline: none;
  border: none;
}
.userpage{
  width: 250px;
  border: 2px solid white;
}
.container{
  width: 100%;
}
.msjcontainer{
  padding-top: 30px;
  height: 85vh;
  overflow: auto;
}


.login{
  position: absolute;
  background: black;
  width: 300px;
  height: 250px;
  top: 50%;
  left: 50%;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
}
.logininput{
  display: inline-block;
  width: 250px;
  font-size:30px;
  margin:50px 25px 0px 25px;
}
.loginbtn{
  display:block;
  font-size:25px;
  width: 80%;
  margin: 0 auto;
}

@media only screen and (max-width:480px){

.msjcontainer{
  width: 100%;
  padding: 10px;
}
.input{
  display: inline-block;
  width: 80%;
  font-size:20px;
  padding: 5px;
  margin-left: 5px;
}
.btn{
  position: relative;
  left: -40px;
  top: 5px;

  font-size:23px;
  background: none;
  padding: 10px;
  outline: none;
  border: none;
}
}
.usersonline{
    overflow-y: auto;
    width: 200px;

}
.user{
    display: flex;
    background: rgba(0, 0, 0, 0.5);
    box-shadow: 2px 2px 2px white;
}
.user .name{
    color: white;
    font-size:20px;
    margin: auto 0px;
    padding: 10px;
}
.image1{
    min-width: 50px;
    border-radius: 50%;
    border: 2px solid green;
    overflow: hidden;
}
.ham{
    color: white;
    font-size: 30px;
    cursor: pointer;


}

@media only screen and (max-width:480px){
    .usersonline{
        position: absolute;
        z-index: 100;
        top: 50px;
        height: 85vh;
        overflow-y: auto;
    }
    .ham{
        display: block;
    }
}
*{
    box-sizing: border-box;
}
.msj{
display: flex;
justify-content: space-between;
position: relative;
margin-bottom: 20px;
}
.msj>.image{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
    height: 75px;
    border-radius: 50%;
    position: relative;
    left: 35px;
    top: -20px;
    z-index: 10;
    height: 90px;
    width: 90px;
    border-radius: 50%;
    border: 5px solid green;
    background: rgb(8, 8, 1);
}
.image>span{
    font-style: italic;
    font-weight: 600;
    color: white;
}
.image img{
    border-radius: 50%;
}
 .msj>.text{
    background: rgba(1, 253, 1,0.7);
    font-family: 'Lexend Deca', sans-serif;
    font-size:20px;
    margin:auto 10px auto 0px; 
    padding: 15px 15px 10px 50px;
    width: calc(100% - 60px);
    position: relative;
    border-radius: 5px 10px 10px 10px;
    color: white;
    box-shadow: 2px 2px 5px 2px black;
}
.msj>.text>span{
    display: inline-block;
    width: 100%;
    text-align: right;
    font-size:12px;
    color: white;
}




.mymsj{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    color: white;
    position: relative;
    margin-bottom: 20px;
    }
    .mymsj >.text{
        background: rgba(87, 0, 72,0.7);
        font-family: 'Lexend Deca', sans-serif;
        font-size:20px;
        margin:auto 0px; 
        padding: 15px;
        width: calc(100% - 60px);
        margin-left: 10px;
        position: relative;
        border-radius: 5px 10px 10px 10px;
        box-shadow: 2px 2px 5px 2px black;
    }
    .mymsj>.text>span{
        display: inline-block;
        width: 100%;
        text-align: right;
        font-size:12px;
    }
    .mymsj>.image{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 5px;
        height: 75px;
        border-radius: 50%;
        position: relative;
        left: -35px;
        top: -25px;
        z-index: 10;
        height: 90px;
        width: 90px;
        border-radius: 50%;
        border: 5px solid green;
        background: rgb(8, 8, 1);
    }
